import React, { useState } from 'react'
import './FlipCard.scss'

function Front({ dateExp, txtFront }) {
    return (
        <div className="front">
            <div className="main-area">
                <div className="blog-post">
                    <p className="date">{dateExp}</p>
                    <p className="blog-content">
                        {txtFront}
                    </p>
                </div>
            </div>
        </div>
    )
}

function Back({ txtBack }) {
    return (
        <div className="back">
            <p>{txtBack}</p>
        </div>
    )
}

export default function FlipCard({ dateExp, txtFront, txtBack }) {
    const [flipped, setFlipped] = useState(false);
    const flip = () => {
        setFlipped(!flipped);
    }

    return (

        <div onMouseEnter={flip} onMouseLeave={flip} className={"card-container" + (flipped ? " flipped" : "")}>
            <Front dateExp={dateExp} txtFront={txtFront}></Front>
            <Back txtBack={txtBack}></Back>
        </div>
    )
}
